.headerArea {
  display: flex;
}

.headerArea ul {
  background: #F5F5F5;
  border: 2px dashed #E0E0E0;
  padding: 10px;
  min-height: 60px;
  font-weight: bold;
  color: #BBBDC1;
  position: relative;
  list-style: none;
  width: 100%;
  z-index: 1;
  height: 100%;
}

.headerArea ul:before {
  position: absolute;
  z-index: -1;
  text-align: center;
  width: 100%;
  left: 0;
  top: calc(50% - 7px);
}

.headerArea li {
  display: inline-block;
  font-size: 12px;
  font-weight: normal;
  color: white;
  background: #1FADD6;
  border-radius: 2px;
  line-height: 25px;
  height: 25px;
  padding: 0 10px;
  margin: 0 5px 5px 0;
  cursor: move;
}

[data-mq="mobile"] > .headerArea:first-child > div:first-child ul:before {
  content: 'TOP LEFT';
}

[data-mq="mobile"] > .headerArea:first-child > div:nth-child(2) ul:before {
  content: 'TOP CENTER';
}

[data-mq="mobile"] > .headerArea:first-child > div:last-child ul:before {
  content: 'TOP RIGHT';
}

[data-mq="mobile"] > .headerArea:nth-child(2) ul {
  text-align: center;
}

[data-mq="mobile"] > .headerArea:nth-child(2) ul:before {
  content: 'MAIN';
}

[data-mq="mobile"] > .headerArea:last-child ul {
  text-align: center;
}

[data-mq="mobile"] > .headerArea:last-child ul:before {
  content: 'BOTTOM';
}

[data-mq="tablet"] > .headerArea:first-child > div:first-child ul:before,
[data-mq="desktop"] > .headerArea:first-child > div:first-child ul:before {
  content: 'TOP LEFT';
}

[data-mq="tablet"] > .headerArea:first-child > div:nth-child(2) ul:before,
[data-mq="desktop"] > .headerArea:first-child > div:nth-child(2) ul:before {
  content: 'TOP CENTER';
}

[data-mq="tablet"] > .headerArea:first-child > div:last-child ul:before,
[data-mq="desktop"] > .headerArea:first-child > div:last-child ul:before {
  content: 'TOP RIGHT';
}

[data-mq="tablet"] > .headerArea:nth-child(2) > div:first-child ul:before,
[data-mq="desktop"] > .headerArea:nth-child(2) > div:first-child ul:before {
  content: 'MAIN LEFT';
}

[data-mq="tablet"] > .headerArea:nth-child(2) > div:nth-child(2) ul:before,
[data-mq="desktop"] > .headerArea:nth-child(2) > div:nth-child(2) ul:before {
  content: 'MAIN CENTER';
}

[data-mq="tablet"] > .headerArea:nth-child(2) > div:last-child ul:before,
[data-mq="desktop"] > .headerArea:nth-child(2) > div:last-child ul:before {
  content: 'MAIN RIGHT';
}

[data-mq="tablet"] > .headerArea:last-child > div:first-child ul:before,
[data-mq="desktop"] > .headerArea:last-child > div:first-child ul:before {
  content: 'BOTTOM LEFT';
}

[data-mq="tablet"] > .headerArea:last-child > div:nth-child(2) ul:before,
[data-mq="desktop"] > .headerArea:last-child > div:nth-child(2) ul:before {
  content: 'BOTTOM CENTER';
}

[data-mq="tablet"] > .headerArea:last-child > div:last-child ul:before,
[data-mq="desktop"] > .headerArea:last-child > div:last-child ul:before {
  content: 'BOTTOM RIGHT';
}



