.codeContainer {
  height: 150px;
  border: 1px solid #E1E1E1;
  position: relative;
  transition: all .5s;
}

.codeContainer:hover .copy {
  background: #343434;
}

.codeContainer:hover .copied:before {
  display: inline-block;
  text-decoration: none;
  position: absolute;
  z-index: 1000001;
  width: 0;
  height: 0;
  color: rgba(0, 0, 0, 0.8);
  pointer-events: none;
  content: "";
  border: 5px solid transparent;
  top: auto;
  right: 50%;
  bottom: -5px;
  margin-right: -5px;
  border-bottom-color: rgba(0, 0, 0, 0.8);
}

.codeContainer:hover .copied:after {
  position: absolute;
  z-index: 1000000;
  padding: 5px 8px;
  font: normal normal 11px/1.5 Helvetica,arial,sans-serif;
  color: #fff;
  text-align: center;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: break-word;
  white-space: pre;
  pointer-events: none;
  content: "Copied !";
  background: rgba(0, 0, 0, 0.8);
  border-radius: 3px;
  -webkit-font-smoothing: subpixel-antialiased;
  display: inline-block;
  transform: translateX(50%);
  top: 100%;
  right: 50%;
  margin-top: 5px;
}

.codeContainer:hover .code {
  color: #343434;
}

.codeContainer .code {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 10px;
  color: #c1c1c1;
  font-size: 12px;
  transition: all .5s;
  user-select: all;
}

.codeContainer .code::selection {
  background: #2F5A59;
  color: white;
}

.codeContainer .copy {
  position: absolute;
  bottom: 0;
  right: 0;
  background: #CCCCCC;
  color: white;
  text-align: center;
  font-size: 16px;
  padding: 5px 10px;
  border-top-left-radius: 10px;
  cursor: pointer;
  transition: background .5s;
  border: 0;
}



