.closeButton {
  display: inline-block;
  color: white;
  font-size: 10px;
  line-height: 10px;
  margin-left: 5px;
  cursor: pointer;
  transition: transform .5s;
  padding: 0;
  background: none;
  border: 0;
}

.closeButton:hover {
  transform: rotate(1turn);
}

.closeButton:focus {
  outline: 1px solid darkblue;
}



